import React, { useState } from "react";
import CssIcon from "./Image/css.png";
import JsIcon from "./Image/js.png";
import HtmlIcon from "./Image/html.png";
import "./Skill.css";

const SkillList = props => {
  return (
    <div className="skillItem">
      <div className="skillFront">
        <div className="skillIcon">
          <img src={props.skillIcon} alt="" />
        </div>
        <div className="skillTitle">{props.skillTitle}</div>
      </div>

      <div className="overlay">
        <div className="skillDesc">{props.skillDesc}</div>
      </div>
    </div>
  );
};

const Button = () => {
  const [scroll, setScroll] = useState("scroll");
  const moveEdu = () => {
    setScroll(window.scroll(0, 1665));
  };

  return (
    <div className="arrowBorder" onClick={() => moveEdu()}>
      <i className="downArrow" />
    </div>
  );
};

const Skill = () => {
  return (
    <div className="skillContainer">
      <div className="skill">
        <div className="skillList">
          <SkillList
            skillIcon={HtmlIcon}
            skillTitle="HTML"
            skillDesc={
              <ul>
                <li>1/5 year experience.</li>
                <li>Apa ya bingung.</li>
                <li>Apa lagi ya.</li>
                <li>Hmmm bingung juga ya.</li>
              </ul>
            }
          />
        </div>

        <div className="skillList">
          <SkillList
            skillIcon={CssIcon}
            skillTitle="CSS"
            skillDesc={
              <ul>
                <li>1/5 year experience.</li>
                <li>Apa ya bingung.</li>
                <li>Apa lagi ya.</li>
                <li>Hmmm bingung juga ya.</li>
              </ul>
            }
          />
        </div>

        <div className="skillList">
          <SkillList
            skillIcon={JsIcon}
            skillTitle="JavaScript"
            skillDesc={
              <ul>
                <li>1/5 year experience.</li>
                <li>Apa ya bingung.</li>
                <li>Apa lagi ya.</li>
                <li>Hmmm bingung juga ya.</li>
              </ul>
            }
          />
        </div>
      </div>
      <Button />
    </div>
  );
};

export default Skill;
