import React, { useState } from "react";
import sd from "./Image/muhammadiyah.jpg";
import smp from "./Image/smp19.jpg";
import sma from "./Image/sman70.jpg";
import binus from "./Image/binus.jpg";
import book from "./Image/book.png";
import location from "./Image/location.png";
import "./Education.css";

const EduImage = props => {
  return (
    <div className="eduImage">
      {" "}
      <img src={props.eduImage} alt="" />
    </div>
  );
};

const EduDesc = props => {
  return (
    <div className="eduDesc">
      <div className="eduName">{props.eduName}</div>
      <div className="eduLoc">
        <img src={location} alt="" />
        {props.eduLoc}
      </div>
      <div className="eduYear">
        <img src={book} alt="" />
        {props.eduYear}
      </div>
    </div>
  );
};

const Button = () => {
  const [scroll, setScroll] = useState("scroll");
  const moveEnd = () => {
    setScroll(window.scroll(0, 2000));
  };

  return (
    <div className="arrowBorder" onClick={() => moveEnd()}>
      <i className="downArrow" />
    </div>
  );
};

const Education = () => {
  return (
    <div className="eduContainer">
      <div className="listContainer">
        <div className="eduList">
          <EduImage eduImage={sd} />
          <EduDesc
            eduName="SD Muhammadiyah 1"
            eduLoc="Samarinda"
            eduYear="2004-2010"
          />
        </div>
        <div className="eduList">
          <EduImage eduImage={smp} />
          <EduDesc
            eduName="SMP Negeri 19"
            eduLoc="Jakarta"
            eduYear="2010-2013"
          />
        </div>
        <div className="eduList">
          <EduImage eduImage={sma} />
          <EduDesc
            eduName="SMA Negeri 70"
            eduLoc="Jakarta"
            eduYear="2013-2016"
          />
        </div>
        <div className="eduList">
          <EduImage eduImage={binus} />
          <EduDesc
            eduName="Binus University"
            eduLoc="Jakarta"
            eduYear="2016-2020"
          />
        </div>
      </div>

      <Button />
    </div>
  );
};

export default Education;
