import React, { useState } from "react";
import Profile from "./Image/Arielfunny.png";
import "./AboutMe.css";

const AboutDesc = () => {
  return (
    <div className="aboutDesc">
      <div className="greeting">Hello!</div>
      <div className="name">I'm Nesia</div>
      <div className="content">
        a computer science student by accident, trying to make the best of it.
        I'm an eager-to-learn person, though needs a lot of guidance. For the
        last 2,5 years of my computer science study, I've grown to not dislike
        HTML and CSS and that is why I'm here, trying to expand my knowledge of
        those things.
      </div>
      <div className="contactButton">
        <a href="mailto:nesia.yunindya@gmail.com" className="contactMe">
          {" "}
          Contact Me
        </a>
      </div>
    </div>
  );
};

const Button = () => {
  const [scroll, setScroll] = useState("scroll");
  const moveSkill = () => {
    setScroll(window.scroll(0, 850));
  };
  return (
    <div className="arrowBorder" onClick={() => moveSkill()}>
      <i className="downArrow" />
    </div>
  );
};

const About = () => {
  return (
    <div className="aboutContainer">
      <div className="aboutMe">
        <AboutDesc />
        <img src={Profile} alt="wait for it" />
      </div>

      <div className="scroll">
        <Button />
      </div>
    </div>
  );
};

export default About;
