const NavItem = props => {
  const { itemName } = props;

  const handleClick = itemName => {
    if (itemName === "About Me") {
      window.scroll(0, 0);
    } else if (itemName === "Skill") {
      window.scroll(0, 850);
    } else if (itemName === "Education") {
      window.scroll(0, 1665);
    }
  };

  return (
    <div className="navItem" onClick={() => handleClick(itemName)}>
      <div>{itemName}</div>
    </div>
  );
};

export default NavBar;
